.chat-wrapper {
  max-width: 350px;
  position: fixed;
  width: 100%;
  bottom: -12px;
  right: 10px;
  z-index: 99;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.chat-section {
  position: relative;
}

.chat-header {
  padding: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-bottom: transparent;
  background: transparent;
  position: absolute;
  top: 12px;
  right: 4px;
  z-index: 999;
}

