  font-size: 20px;
}

.chat-header .close-icon {
  width: 25px;
  height: 25px;
  color: black;
  background: #f9ff00;
  text-align: center;
  line-height: 44px;
  border-radius: 100%;
  transition: all 0.5s;
  cursor: pointer;
  position: relative;
  bottom: 10px;
  display: grid;
  place-content: center;
}

